body {
  --active-font-color: #00acdc;
  margin: 0;
  font-family: Roboto, Arial, Helvetica, sans-serif, serif;
  font-size: 12px;
  font-weight: 500;
}

.page__header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  justify-content: center;
  align-items: center;
  display: flex;
}

.header__logo__image {
  width: 40px;
  height: 40px;
}

.nav__list {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  justify-content: center;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: var(--active-font-color);
}

.nav__element {
  margin-left: 20px;
}

.nav__element:first-child {
  margin-left: 0;
}

.is-active {
  color: var(--active-font-color);
  position: relative;
}

.is-active:after {
  content: "";
  background: var(--active-font-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  top: 56px;
}
